import React from 'react';
import { Link } from 'react-router-dom';

import styles from './styles.module.scss';

class LessonItem extends React.Component{
    constructor(props){
        super(props);
        this.img = props.data.img;
        this.title = props.data.title;
    }

    render(){
        console.log(this.props.data);
        return (
            <div className={styles["lesson"]}>
                <div className={styles["container"]}>
                    <Link to={this.props.data.route}>
                        <div className={styles["header"]}>
                            <div className={styles["index"]}>{this.props.index}</div>
                            <img src={this.props.data.img}/><div className={styles["title"]}>{this.props.data.title}</div>
                        </div>
                    </Link>
                    <div className={styles["description"]}>{this.props.data.description}</div>
                    <div className={styles["list"]}>
                        {this.props.data.pages.map((x)=>(<Link to={x.route}>{x.title}</Link>))}
                    </div>
                </div>
            </div>
        )
    }
}

export default LessonItem;