import React from 'react';
import {Link} from 'react-router-dom';

import styles from './styles.module.scss';
import image from './saly.png';
import Calculator from 'components/calculator';
import loadData from 'services/storage';
import { NextLesson } from 'components/next-lesson';
import LessonItem from 'components/lesson-item';

import left from './left.svg';
import right from './right.svg';

class Home extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      lessons: [],
      nextLesson: {
        message: 'Curious to know what happened?',
        title: '',
        route: '/',
        showNext: false
      }
    }
  }

  componentDidMount(){
    loadData().then((_updatedLessons)=>{
      this.setState({lessons: _updatedLessons});
      var found = false;
      var i=0;
      for(var lesson of _updatedLessons){
        for(var page of lesson.pages){
          if(page.status=='unread'){
            var message = 'Pick up where you left off';
            if(i==0)
              message = 'Curious to know what happened?';
            this.setState({nextLesson:{title: page.title, route:page.route, message: message, showNext: true}});
            found=true;
            break;
          }
          i++;
        }
        if(found) break;
      }
      if(!found){
        this.setState({nextLesson:{title: "You're all done!", route: "#", message: "", showNext: false}});
      }
    });
  }

  render(){
    var lessonComponents = this.state.lessons.map((x,i)=>{
      return (<LessonItem data={x} index={i+1}/>);
    })
    return (
      <div className={styles["home"]}>
        <div className={styles["banner-container"]}>
          <img className={styles["left-img"]} src={left}/>
          <img className={styles["right-img"]} src={right}/>
          <div className={styles["banner"]}>
            <div className={styles["dynamic"]}>
              <h3>Ordering pizza?</h3>
              <h3>Making coffee?</h3>
              <h3>Browsing netflix?</h3>
            </div>
            <h1>Learn to invest<br/>faster than that.</h1>
            <p>Simple lessons that take you less than 2 minutes.<br/>No jargon, no bullshit. Guaranteed</p>
            {/* <h5>You won‚Äôt need a financial advisor when you are done with us üòâ</h5> */}
          </div>
        </div>
        <div className={styles["worth"]}>
          <div className={styles["left"]}>
            <h1>Why you should invest</h1>
            <p>Because you‚Äôre literally losing money when you keep it under your matress or in your bank account.</p><br/>
            <p>How much? See for yourself üíØ</p>
          </div>
          <div className={styles["right"]}>
          <Calculator toggle/>
          </div>
        </div>
        <div className={styles["lessons"]}>
          <div className={styles["next-lesson-container"]}>
            <p>{this.state.nextLesson.message}</p>
            <NextLesson text={this.state.nextLesson.title} route={this.state.nextLesson.route} showNext={this.state.nextLesson.showNext}/>
          </div>
          <br/>
          <h1>Lessons</h1>
          {lessonComponents}
        </div>
      </div>
    );
  }
}

export default Home;
